* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --blue: #2374e1;
  --blue-on-hover: #1664ca;
  --green: #00a400;
  --green-on-focus: #2b9217;
  --green-on-hover: #068a06;
  --red: rgb(220, 20, 60);
  --red-on-hover: rgb(204, 27, 62);
  --font-logo: 'Titillium Web', sans-serif;
}

@media (prefers-color-scheme: dark) {
  :root {
    color: #eee;
    --main-bg-color: #18191a;
    --secondary-bg-color: #222;
    --main-text-color: rgb(218, 220, 224);
    --text-reverse-color: #111;
    --modal-bg-color: #222;
    --form-input-text: #f8f8f8;
    --form-border-color: 1px solid #333;
    --btn-text: #c4c4c4;
    --btn-bg: #c4c4c44b;
    --header-bg: #222;
    --gray-hover: rgb(47, 48, 49);
  }
}

@media (prefers-color-scheme: light) {
  :root {
    color: #111;
    --main-bg-color: #f8f8f8;
    --secondary-bg-color: #ffffff;
    --main-text-color: rgb(31, 31, 31);
    --text-reverse-color: #eee;
    --modal-bg-color: #f8f8f8;
    --form-input-text: #111;
    --form-border-color: 1px solid rgb(221, 223, 226);
    --btn-text: #111;
    --btn-bg: #c4c4c480;
    --header-bg: #f4f4f4;
    --gray-hover: #eee;
  }
}

body {
  margin: 0;
  background: var(--main-bg-color);
  font-family: 'Oxygen', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style: none;
}

a:not(:hover) {
  text-decoration: none;
}

button,
input,
textarea {
  border: 0;
  font-family: inherit;
}

fieldset {
  border: 0;
}

button {
  cursor: pointer;
  font-weight: bold;
  background: transparent;
}

main {
  padding: 1rem;
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 600px) {
  main {
    max-width: 600px;
  }
}
